import * as React from 'react';
import * as moment from 'moment';
import * as classNames from 'classnames';
import {Link} from 'react-router-dom';

import './NavMenu.css';

import {
    Match,
    BBL_DURATION_MINS
} from '../../Data/Matches';
import {UI_MENU_DATE_FORMAT} from '../../Utils/DateUtil';
import {timeNow} from '../../Utils/TimeUtil';

export interface NavMenuProps {
    onSelectFixture: (fixture: Match) => void;
    selectedFixture: Match;
    fixtures: Match[];
}

export default class NavMenu extends React.PureComponent<NavMenuProps> {
    constructor(props: NavMenuProps) {
        super(props);
    }

    public render() {
        return (
            this.renderMatchList()
        );
    }

    private renderMatchList() {
        return this.props.fixtures.map((match, index) => {
            const time = moment(match.date).utcOffset(match.ground.state.timeZone * 60).local();
            const selected: boolean = match.matchNumber === this.props.selectedFixture.matchNumber;
            const selectedElement = selected ? true : undefined;
            const complete: boolean = time.clone().add(BBL_DURATION_MINS, 'minutes').isBefore(timeNow());
            const listClasses = classNames(
                'nav__item', {
                    'nav__item--selected': selected,
                    'nav__item--complete': complete
                }
            );
            return (
                <Link
                    to={match.url}
                    key={index + '-' + match.date.toDateString()}
                    className="nav__link"
                >
                    <li
                        className={listClasses}
                        onClick={() => this.props.onSelectFixture(match)}
                        data-selected={selectedElement}
                    >
                        <div className="nav__item_container">
                            <div className="nav__item_number">
                                {match.matchNumber} :
                            </div>
                            <div className="nav__item_details">
                                <div className="nav__item_teams">
                                    {match.homeTeam.shortName} -vs- {match.awayTeam.shortName}
                                </div>
                                <div className="nav__item_venue">
                                    {match.ground.name}, {match.ground.suburb}, {match.ground.state.name}
                                </div>
                                <div className="nav__item_date_time">
                                    {time.format(UI_MENU_DATE_FORMAT)}
                                </div>
                            </div>
                        </div>
                    </li>
                </Link>
            );
        });
    }
}


